body {
  margin: 0;
  padding: 0;
  height: 100%;
  margin-bottom: 100px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer{
  height: 30px;
  margin-top: 50px;
  padding-top: 50px;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 0;
}
#nav-fix{  
  background-color: #333;
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */
  z-index: 1;
}
#dropdown{
  z-index: 1000;
}
.dropdown-menu{  
  position: absolute;
  
}

#user_img{
  width: 30px;
  height: 30px;
}
#label-upload{
  background-color: #1A69DA;  
}

#label-upload:hover{
  background-color: #134792;
}

input:focus {
    outline:none;
}

#video_name{
  font-size: 12px;
}


#video_path{
  font-size: 12px;
}


#video{
  padding-top: -10%;
  max-height: 200px;
  width: 100%;
  background-color: black;
}
Card{
  margin-bottom: 5vh;  
}
#flex{
  display: -webkit-flex;
  display: flex;
}

#flex-container{
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
#loading{
  color: #ffffff;
}

#card-footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.card-footer-div {
  margin-right: auto;
}
#footer-img{
  text-align: right;
  margin-right: 5px;
  width: 16px;
  height: 16px;
}

#mario-chat{
max-width: 600px;
width: 300px;
height: 75vh;
margin: -1.5rem auto -3rem auto;
border: 1px solid #ddd;
box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
border-radius: 2px;
position: fixed;
top: 85px;
right: 0;
background-color: #fff;
}

#chat-window{
height: 60vh;
overflow: auto;
background: #f9f9f9;
}

#output p{
padding: 14px 0px;
margin: 0 20px;
border-bottom: 1px solid #e9e9e9;
color: #555;
}

#feedback p{
color: #aaa;
padding: 14px 0px;
margin: 0 20px;
}

#output strong{
color: #3f3a3a;
}

label{
box-sizing: border-box;
display: block;
padding: 10px 20px;
}

textarea{
padding: 10px 20px;
box-sizing: border-box;
background: #eee;
border-radius: 5px;
border: 1 black solid;
display: block;
width: 100%;
background: #fff;
border-bottom: 1px solid #eee;
font-family: Nunito;
font-size: 16px;
}

#title-span{
  display: inline-block
}
#title {
  display: inline-block;
  margin-right: 5rem;
}

@media screen and (max-width: 1200px) {
  #chat-window{
  height: 60vh;
  overflow: auto;
  background: #f9f9f9;
  }
}

@media screen and (max-width: 750px) {
  #mario-chat{
    margin-top: 296px;
    width: 100%;
    height: 30vh;
    border: 1px solid #ddd;
    box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
    border-radius: 2px;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    }
    #chat-window{
      height: 25vh;
      overflow: auto;
      background: #f9f9f9;
      }
}

#row{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}
#streamVideo{
  margin-top: 5vh;
