  -webkit-flex: 0 0 75%;
          flex: 0 0 75%;
  max-width: 75%;
}

#chat{
  -webkit-flex: 0 0 25%;
          flex: 0 0 25%;
  max-width: 25%;
}
@media screen and (max-width: 750px) {
